import React from "react";
import TextyAnim from 'rc-texty';
require('rc-texty/assets/index.css');

export default function Texty(props) {
  const {mode,type,children} = props
  const getSplit = e => {
    const t  = e.split('.');
    const c = [];
    t.forEach((str, i) => {
      c.push((
        <span key={`${str}-${i}`}>
          {str}
        </span>
      ));
      if (i < t.length - 1) {
        c.push(<span key={` -${i}`}> </span>);
      }
    });

    return c
  }
  return (
    <TextyAnim
      mode={mode}
      type={type}
      split={getSplit}
    >
      {children}
    </TextyAnim>
  );
}
