import React, {useState, useEffect, useContext} from "react";
import Demo from './Texty';

import { Contexto } from 'Context';

//core
import Header from "components/Header/Header.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import Parallax from "components/Parallax/Parallax.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import MaterialSlider from "./Slider";

// import AwesomeSlider from 'react-awesome-slider';
import AwesomeSlider from 'react-awesome-slider';
import withAutoplay from 'react-awesome-slider/dist/autoplay';
import withCaption from 'react-awesome-slider/dist/captioned';
import 'react-awesome-slider/dist/captioned.css';

import 'react-awesome-slider/dist/styles.css';
import styles from "./style2.css";

import img1 from "assets/images/banner/1.jpg";
import img2 from "assets/images/banner/2.jpg";
import img3 from "assets/images/banner/3.jpg";
import img6 from "assets/images/banner/6.jpg";

//config
import config from "../../config/config";
const dashboardRoutes = [];

const AutoplaySlider = withAutoplay(AwesomeSlider);
const CaptionedSlider = withCaption(AutoplaySlider);

export default function Cronologia(props){
	const Logo = config.header.logo;
	const { ...rest } = props;
	const {classes,contenido} = props;
	const contexto = useContext(Contexto);
	const configuracion = {
		mode : ['smooth', 'sync'],
		type:['left','right','top','bottom', 'alpha', 'mask-bottom', 'mask-top']
	}

	function randomInteger(min, max) {
	  return Math.floor(Math.random() * (max - min + 1)) + min;
	}

	const marcas = contexto.contenido.introduccion[contexto.idioma].marcas;
	const newMarcas =  marcas.map( (o,i) => {
		const nro1 = randomInteger(0,1);
		const nro2 = randomInteger(0,5);
		if(o.parrafo)
			return { ...o, parrafo: (<Demo type={configuracion.type[nro2]} mode={configuracion.mode[nro1]} key={"Demo_"+i}>{o.parrafo}</Demo>)}
		return o
	})

return(
	<React.Fragment>
		      <Header
		        color={config.header.color}
		        routes={dashboardRoutes}
		        brand={contenido.titulo}
		        rightLinks={<HeaderLinks />}
				fixed
				logo={Logo}
		        changeColorOnScroll={contenido.changeColorOnScroll}
		        {...rest}
		      />
			<Parallax image={""}>
					<CaptionedSlider 
						onTransitionRequest = { ({ currentSlide, currentIndex }) => {} }
						range = {false}
						Mslider={MaterialSlider}
						MsliderShow={true}
						bullets = {true}
						interval={6000}
						play={true}
						fillParent={true}
						screens={newMarcas}
						cssModule={styles}>
					  </CaptionedSlider>
			</Parallax>
		</React.Fragment>
	)
}