export default function DiscreteSlider(props) {
  
  const [ value, setValue ] = useState(0);
  const [label, setLabel] = useState(0)
  const contexto = useContext(Contexto);

  const [ idioma, setIdioma ] = useState(contexto.idioma);
  const [ introduccion, setIntroduccion ] = useState(contexto.contenido.introduccion[idioma].marcas);


  const classes = useStyles();

  React.useEffect( () => {
    if(props.indice || props.indice===0)
      setValue(props.indice)  
  },[props.indice])
  
  const valueLabelFormat = x => {
    // return value
    const label = introduccion.filter( o => o.value===x)[0].label
    return label
  };

  return (
    <div className={classes.root}>
      <Slider
        defaultValue={0}
        aria-labelledby="discrete-slider-custom"
        step={1}
        value={value}
        marks={introduccion}
        classes={classes}
        valueLabelDisplay="on"
        valueLabelFormat={valueLabelFormat}
        max={introduccion.length-1}
        onChangeCommitted = { (object , value) => {
          //console.log("onChangeCommitted " + typeof(value),value);
          props.funcion(value)
        }}
        onChange={ (event,value) => {
            props.funcion(value)
        }}
      />
    </div>
  );
}
